//ListMode
<template>
  <div class="content-area">
    <!-- 左側區塊 -->
    <div class="left-content">
      <!-- 左側內容 -->
      <!-- 在這裡放置左側區塊的內容 -->
    </div>

    <!-- 右側區塊 -->
    <div class="right-content">
      <!-- 下拉選單及切換模式的按鈕 -->
 

    <!-- 根據模式顯示內容 -->
    <div class="mode-content">
      <div v-if="currentMode === 'list'" class="list-mode-container">
        <!-- ListMode 組件 -->
        <ListMode :items="items" />
      </div>
      <div v-else-if="currentMode === 'table'" class="table-mode-container">
        <!-- TableMode 組件 -->
        <TableMode :items="items" />
      </div>
    </div>

      <!-- 餐廳列表 -->
      <div class="restaurant-container">
        <div class="content-wrapper">
          <!-- 加載指示器和錯誤訊息 -->
          

          <!-- 餐廳項目 -->
          <div v-for="item in pagedData" :key="item.ID" class="restaurant-item">
            <!-- 位置信息 -->
            <div class="location-info">
              <div class="city-label highlight-city">{{ item.City }}</div>
              <div class="town-label">{{ item.Town }}</div>
            </div>
            <!-- 餐廳內容 -->
            <div class="restaurant-content">
              <div class="restaurant-image">
                <div class="image-container">
                  <img :src="item.PicURL" alt="Restaurant Image">
                </div>
              </div>
              <div class="restaurant-info">
                <div class="restaurant-title">
                  <div class="restaurant-name" :style="{ color: item.Url ? 'rgb(12, 164, 211)' : 'black' }" @click="openUrl(item.Url)">{{ item.Name }}</div>
                  <div class="restaurant-description" v-html="item.FoodFeature.replace(/<br><br>/g, '')"></div>
                </div>
              </div>
            </div>
          </div>

          <!-- 分頁控件 -->
          <div class="pagination">
            <div class="page-info">美食頁次 {{ currentPage }} / {{ totalPages }}</div>
            <div class="page-buttons">
              <button v-for="pageNumber in totalPages" :key="pageNumber" @click="gotoPage(pageNumber)" :disabled="currentPage === pageNumber" class="page-button" :class="{ active: currentPage === pageNumber }">{{ pageNumber }}</button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- AdvertisementBlock 組件 -->
    <AdvertisementBlock :advertisements="advertisements" :isAdFixed="isAdFixed" />
  </div>
</template>

<script>
import axios from 'axios';
// eslint-disable-next-line no-unused-vars
import ListMode from './ListMode.vue';
// eslint-disable-next-line no-unused-vars
import TableMode from './TableMode.vue';


export default {
  name: 'ContentArea',
  props: {
    cities: Array,
    towns: Object,
  },
  data() {
    return {
      allData: [],
      currentPage: 1,
      itemsPerPage: 10,
      loading: false,
      error: null,
      selectedCity: '', // 預設為空字串
      selectedTown: '', // 預設為空字串
      mode: 'list', // 默認為列表模式
    };
  },
  computed: {
    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage);
    },
    filteredData() {
      const city = this.selectedTown ? this.selectedCity : this.selectedCity || '';
      const town = this.selectedTown || '';
      return this.allData.filter(
        (item) =>
          (!city || item.City === city) &&
          (!town || item.Town === town)
      );
    },
    pagedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredData.slice(start, end);
    },
  },
  watch: {
    selectedCity(newVal, oldVal) {
      if (newVal !== oldVal && newVal !== '') {
        this.currentPage = 1;
        this.fetchData();
      }
    },
    selectedTown(newVal, oldVal) {
      if (newVal !== oldVal && newVal !== '') {
        this.currentPage = 1;
        this.fetchData();
      }
    },
    mode(newVal, oldVal) {
      if (newVal !== oldVal) {
        // 模式切換後的處理邏輯，可以根據需要進行修改
        console.log('切換模式到:', newVal);
      }
    },
  },
  created() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        this.loading = true;
        const response = await axios.get(
          'https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx'
        );
        this.allData = response.data.map((item) => ({
          ID: item.ID,
          Name: item.Name,
          Address: item.Address,
          Tel: item.Tel,
          HostWords: item.HostWords,
          Price: item.Price,
          OpenHours: item.OpenHours,
          CreditCard: item.CreditCard,
          TravelCard: item.TravelCard,
          TrafficGuidelines: item.TrafficGuidelines,
          ParkingLot: item.ParkingLot,
          Url: item.Url,
          Email: item.Email,
          PetNotice: item.PetNotice,
          Reminder: item.Reminder,
          FoodMonths: item.FoodMonths,
          FoodCapacity: item.FoodCapacity,
          FoodFeature: item.FoodFeature,
          City: item.City,
          Town: item.Town,
          PicURL: item.PicURL,
          Latitude: item.Latitude,
          Longitude: item.Longitude,
          BlogUrl: item.BlogUrl,
        }));
      } catch (error) {
        console.error('获取数据时发生错误: ', error);
        this.error = '获取数据时发生错误，请稍后重试。';
      } finally {
        this.loading = false;
      }
    },
    gotoPage(pageNumber) {
      if (pageNumber > 0 && pageNumber <= this.totalPages) {
        this.currentPage = pageNumber;
      }
    },
    handleCityChange(event) {
      this.selectedCity = event.target.value;
      this.selectedTown = '';
      this.currentPage = 1;
      this.fetchData();
    },
    handleTownChange(event) {
      this.selectedTown = event.target.value;
      this.currentPage = 1;
      this.fetchData();
    },
    openUrl(url) {
      if (url) {
        window.open(url, '_blank');
      }
    },
    toggleMode() {
      this.mode = this.mode === 'list' ? 'table' : 'list';
    },
  },
};
</script>

<style lang="scss">
/* 主要内容区 */
.content-area {
  display: flex;
  justify-content: space-between; /* 将内容区域两侧对齐 */
}

.left-content {
  /* 根据需要设置左侧内容区域的样式 */
}

.right-content {
  flex-grow: 1; /* 右侧内容区域占据剩余空间 */
}

.dropdowns {
  display: flex;
  align-items: center;
  margin-bottom: 20px; /* 下拉菜单与餐厅列表之间的间距 */
}

.dropdown {
  appearance: none;
  border: 2px solid #3498db;
  background-color: #fff;
  color: #3498db;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  transition: border-color 0.3s, color 0.3s;
}

.dropdown:hover,
.dropdown:focus {
  border-color: #2980b9;
}

.dropdown option {
  background-color: #fff;
  color: #3498db;
  font-size: 16px;
}

.dropdown:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.restaurant-container {
  margin-left: 20px;
}

.restaurant-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 20px;
}

.location-info {
  display: flex;
  flex-direction: column;
  margin-right: 10px; /* 调整位置信息的右边距 */
}

.city-label {
  display: block;
  margin-bottom: 5px;
  background-color: rgb(12, 164, 211);
  color: white;
  padding: 2px 4px;
  border-radius: 4px;
}

.city-label.highlight-city {
  display: block !important;
  white-space: nowrap;
}

.town-label {
  display: block;
  writing-mode: vertical-rl;
  white-space: nowrap;
  color: #999999;
  font-size: 16px;
  margin-right: 10px;
  letter-spacing: 5px;
}

.restaurant-content {
  display: flex;
  align-items: center;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 8px;
  width: 100%;
  box-sizing: border-box;
}

.restaurant-image {
  margin-right: 20px;
}

.restaurant-info {
  flex: 1;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.restaurant-description {
  font-size: 16px;
  line-height: 1.6;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  white-space: normal;
  margin-top: 10px;
}

.restaurant-title {
  margin-bottom: 15px;
}

.restaurant-name {
  font-weight: bold;
  font-size: 26px;
  text-align: left;
  color: black;
  transition: color 0.3s ease;
}

.restaurant-name:hover {
  color: rgb(12, 164, 211);
}

.pagination {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px; /* 分页控件与餐厅列表之间的间距 */
}

.page-info {
  margin-right: 10px;
}

.page-buttons {
  display: flex;
}

.page-button {
  margin: 0 5px;
  padding: 5px 10px;
  border: 1px solid #ccc;
  background-color: #ccc;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s, color 0.3s;
}

.page-button:hover:not(:disabled) {
  background-color: #e0e0e0;
  color: white;
}

.page-button:disabled {
  cursor: not-allowed;
  color: #999;
}

.page-button.active {
  background-color: rgb(12, 164, 211);
  font-weight: bold;
  color: white;
}

.image-container {
  max-width: 200px;
  max-height: 150px;
  overflow: hidden;
}

.image-container img {
  width: 100%;
  height: auto;
}

.loading-indicator {
  text-align: center;
  font-size: 18px;
  margin: 20px 0;
}

.error-message {
  text-align: center;
  color: red;
  font-size: 18px;
  margin: 20px 0;
}
</style>
