//卡片模式（CardMode.vue）
<template>
  <div class="card-mode">
    <div class="card" v-for="(item, index) in pagedData" :key="index" @click="openLink(item)">
      <img :src="item.picURL" alt="card image">
      <div class="card-info">
        <h3>{{ item.name }}</h3>
        <p>早安 我是卡片模式</p>
        <p>{{ item.description }}</p> <!-- 新增的文字內容示例 -->
      </div>
    </div>
    <p>早安 我是卡片模式</p>
  </div>
</template>

<script>
export default {
  name: 'CardMode',
  props: {
    pagedData: {
      type: Array,
      required: true
    }
  },
  methods: {
    openLink(item) {
      if (item.url) {
        window.open(item.url, '_blank');
      }
    }
  }
};
</script>

<style scoped>
.card-mode {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.card {
  width: calc(33.333% - 20px);
  margin-bottom: 20px;
  cursor: pointer;
  position: relative;
}

.card img {
  width: 100%;
  height: auto;
}

.card-info {
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.7);
  color: #fff;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  opacity: 0;
  transition: all 0.3s ease;
}

.card-info h3, .card-info p {
  margin: 0;
}

.card:hover .card-info {
  opacity: 1;
}
</style>
