<template>
  <div class="content-area">
    <!-- 控制項區域 -->
    <div class="controls">
      
      <!-- 下拉選單及切換模式的按鈕 -->
      <div class="dropdowns">
        <!-- 城市下拉選單 -->
        <select v-model="selectedCity" @change="handleCityChange" class="dropdown">
          <option value="" disabled selected>請選擇縣市</option>
          <option v-for="city in cities" :key="city" :value="city">{{ city }}</option>
        </select>

        <!-- 鄉鎮下拉選單 -->
        <select v-model="selectedTown" @change="handleTownChange" class="dropdown">
          <option value="" disabled selected>請選擇鄉鎮市</option>
          <template v-if="selectedCity">
            <option v-for="town in towns[selectedCity]" :key="town" :value="town">{{ town }}</option>
          </template>
        </select>
      </div>

      <!-- 切換模式的按鈕與下拉選單 -->
      <div class="mode-switch">
        <span>檢視模式:</span>
        <button @click="currentMode = 'list'" class="mode-button">
          <img src="@/assets/list-mode.png"  alt="列表模式">
        </button>
        <button @click="currentMode = 'table'" class="mode-button">
          <img src="@/assets/table-mode.png" alt="表格模式">
        </button>
        <button @click="currentMode = 'card'" class="mode-button">
          <img src="@/assets/card-mode.png" alt="卡片模式">
        </button>
      </div>

      <!-- 下拉選單 -->
      <select v-if="dropdowns.length > 0" v-model="selectedItem" @change="handleDropdownChange">
        <option v-for="option in dropdowns" :key="option.id" :value="option.value">
          {{ option.label }}
        </option>
      </select>
    </div>

    <!-- 根據模式顯示內容 -->
    <div class="mode-content">
      <div v-if="currentMode === 'list'" class="list-mode-container">
        <!-- ListMode 組件 -->
        <ListMode :items="items" />
      </div>
      <div v-else-if="currentMode === 'table'" class="table-mode-container">
        <!-- TableMode 組件 -->
        <TableMode :items="items" />
      </div>
      <div v-else-if="currentMode === 'card'" class="card-mode-container">
        <!-- CardMode 組件（如果有） -->
        <CardMode :items="items" />
      </div>
    </div>

    <!-- AdvertisementBlock 組件 -->
    <AdvertisementBlock :advertisements="advertisements" :isAdFixed="isAdFixed" />
  </div>
</template>

<script>
import ListMode from './ListMode.vue';
import TableMode from './TableMode.vue';
import CardMode from './CardMode.vue'; // 如果有卡片模式組件的話，請導入
import AdvertisementBlock from './Advertisement.vue';
import axios from 'axios';

export default {
  name: 'ContentArea',
  components: {
    ListMode,
    TableMode,
    CardMode, // 如果有卡片模式組件的話，請加入這裡
    AdvertisementBlock,
  },
  props: {
    items: {
      type: Array,
      required: true,
    },
    advertisements: {
      type: Array,
      default: () => [],
    },
    isAdFixed: {
      type: Boolean,
      default: false,
    },
    dropdowns: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
      currentMode: 'list', // 默認列表模式
      selectedItem: null, // 選擇的下拉選項
      cities: [], // 城市選項
      towns: {}, // 鄉鎮市選項
      selectedCity: '', // 選擇的城市
      selectedTown: '', // 選擇的鄉鎮市
    };
  },
  created() {
    this.fetchCityAndTownOptions();
  },
  methods: {
    async fetchCityAndTownOptions() {
      try {
        const response = await axios.get("https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx");
        const data = response.data;

        // 處理從 API 返回的資料，這裡假設資料格式為 JSON 並包含 cities 和 towns 選項
        this.cities = data.cities;
        this.towns = data.towns;

      } catch (error) {
        console.error('Error fetching city and town options:', error);
      }
    },
    handleCityChange() {
      // 處理城市選擇變更事件
      // 根據選擇的城市更新相應的鄉鎮市選項
      this.selectedTown = ''; // 清空選擇的鄉鎮市
    },
    handleTownChange() {
      // 處理鄉鎮市選擇變更事件
    },
    handleDropdownChange() {
      // 處理下拉選單選擇事件
      console.log('Selected item:', this.selectedItem);
    },
  },
};
</script>

<style scoped>
.content-area {
  display: flex;
  flex-wrap: wrap;
}

.controls {
  display: flex;
  justify-content: space-between; /* 將內容分散對齊 */
  align-items: center; /* 垂直居中對齊 */
  width: 100%; /* 填滿父容器的寬度 */
  margin-bottom: 20px; /* 底部間距 */
}

.mode-switch {
  display: flex;
  align-items: center; /* 垂直居中對齊 */
  margin-left: auto; /* 將模式切換區塊推到右邊 */
}

.mode-switch span {
  margin-right: 10px;
}

.mode-switch .mode-button {
  background: none;
  border: none;
  cursor: pointer;
}

.mode-switch .mode-button img {
  width: 30px; /* 根據需要調整圖片大小 */
  height: 30px;
}

.mode-switch .mode-button:not(:last-child) {
  margin-right: 10px;
}

.mode-content {
  flex: 1; /* 調整模式內容區域的寬度，這裡假設佔比為1 */
}

.table-mode-container,
.list-mode-container,
.card-mode-container {
  margin-bottom: 20px;
}
</style>
