<template>
  <div id="app">
    <!-- 頁面標題區域 -->
    <HeaderMenu/>
    
    <!-- 主要內容區域 -->
    <div class="main-content">
      
      <!-- 資料展示區域 -->
      <div class="data-area">
        <ContentArea
          :items="pagedData"
          :dropdowns="dropdowns"
          :currentPage="currentPage"
          :totalPages="totalPages"
          :loading="loading"
          @gotoPage="gotoPage"
        />
      </div>
      
      <!-- 右側廣告區域 -->
      <div class="advertisement-area">
        <AdvertisementBlock :advertisements="advertisements" />
      </div>
    </div>
    
    <!-- 頁腳資訊 -->
    <FooterInfo />
  </div>
</template>

<script>
import axios from "axios";
import HeaderMenu from "./HeaderMenu.vue";
import ContentArea from "./ContentArea.vue";
import AdvertisementBlock from "./Advertisement.vue";
import FooterInfo from "./FooterInfo.vue";

export default {
  name: "App",
  components: {
    HeaderMenu,
    ContentArea,
    AdvertisementBlock,
    FooterInfo
  },
  data() {
    return {
      selectedCity: null, // 選擇的城市
      selectedTown: null, // 選擇的鄉鎮
      allData: [], // 所有原始資料
      cities: [], // 城市列表
      towns: {}, // 鄉鎮列表
      currentPage: 1, // 當前頁碼
      itemsPerPage: 10, // 每頁顯示的項目數
      loading: false, // 資料加載狀態
      advertisements: [ // 廣告圖片列表
        { src: require("./assets/adv1.png") },
        { src: require("./assets/adv2.png") },
        { src: require("./assets/adv3.png") }
      ],
      dropdowns: [] // 下拉選單選項列表
    };
  },
  computed: {
    totalPages() {
      return Math.ceil(this.filteredData.length / this.itemsPerPage); // 計算總頁數
    },
    filteredData() {
      return this.allData.filter(item => (
        (!this.selectedCity || item.City === this.selectedCity) && // 根據選擇的城市篩選
        (!this.selectedTown || item.Town === this.selectedTown) // 根據選擇的鄉鎮篩選
      ));
    },
    pagedData() {
      const start = (this.currentPage - 1) * this.itemsPerPage;
      const end = start + this.itemsPerPage;
      return this.filteredData.slice(start, end); // 分頁顯示的資料
    },
  },
  watch: {
    selectedCity(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.selectedTown = null; // 重置選擇的鄉鎮
        this.updateTotalPages(); // 更新總頁數
      }
    },
    selectedTown(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.updateTotalPages(); // 更新總頁數
      }
    },
  },
  created() {
    this.fetchData(); // 初始化時加載資料
  },
  methods: {
    updateTotalPages() {
      this.totalPages = Math.ceil(this.filteredData.length / this.itemsPerPage); // 更新總頁數
    },
    async fetchData() {
      try {
        this.loading = true; // 開始資料加載
        const response = await axios.get("https://data.moa.gov.tw/Service/OpenData/ODwsv/ODwsvTravelFood.aspx"); // 發送資料請求
        this.allData = response.data.map(item => ({
          // 整理接收到的資料
          ID: item.ID,
          Name: item.Name,
          Address: item.Address,
          Tel: item.Tel,
          HostWords: item.HostWords,
          Price: item.Price,
          OpenHours: item.OpenHours,
          CreditCard: item.CreditCard,
          TravelCard: item.TravelCard,
          TrafficGuidelines: item.TrafficGuidelines,
          ParkingLot: item.ParkingLot,
          Url: item.Url,
          Email: item.Email,
          PetNotice: item.PetNotice,
          Reminder: item.Reminder,
          FoodMonths: item.FoodMonths,
          FoodCapacity: item.FoodCapacity,
          FoodFeature: item.FoodFeature,
          City: item.City,
          Town: item.Town,
          PicURL: item.PicURL,
          Latitude: item.Latitude,
          Longitude: item.Longitude,
          BlogUrl: item.BlogUrl,
        }));
        this.updateCitiesAndTowns(); // 更新城市和鄉鎮列表
        this.updateTotalPages(); // 更新總頁數
      } catch (error) {
        console.error("There was an error fetching the data: ", error); // 處理資料請求錯誤
      } finally {
        this.loading = false; // 結束資料加載
      }
    },
    updateCitiesAndTowns() {
      // 更新城市和鄉鎮列表
      this.cities = [...new Set(this.allData.map(item => item.City))];
      this.towns = this.allData.reduce((acc, item) => {
        if (!acc[item.City]) {
          acc[item.City] = [];
        }
        if (!acc[item.City].includes(item.Town)) {
          acc[item.City].push(item.Town);
        }
        return acc;
      }, {});
    },
    gotoPage(pageNumber) {
      this.currentPage = pageNumber; // 跳轉到指定頁碼
    },
  },
};
</script>

