<template>
  <div class="advertisement-wrapper" ref="wrapper">
    <div
      v-for="(adv, index) in advertisements"
      :key="index"
      :class="{ 'fixed-advertisement': shouldFixAd(index) }"
      class="advertisement"
      :style="adStyles[index]"
      ref="advertisements"
    >
      <img :src="adv.src" :alt="'Advertisement ' + (index + 1)">
    </div>
  </div>
</template>

<script>
export default {
  name: "AdvertisementBlock",
  props: {
    advertisements: Array
  },
  data() {
    return {
      adTopOffsets: [], // 存储每个广告容器顶部偏移量
      adContainerTop: 0, // 广告容器顶部偏移量
      adContainerHeight: 0, // 广告容器高度
      lastScrollTop: 0, // 上一次滚动位置
      adStyles: [] // 存储每个广告的样式
    };
  },
  mounted() {
    this.$nextTick(() => {
      this.adTopOffsets = this.getAdTopOffsets(); // 获取每个广告容器顶部偏移量
      this.adContainerTop = this.$refs.wrapper.offsetTop; // 获取广告容器顶部偏移量
      this.adContainerHeight = this.$refs.wrapper.offsetHeight; // 获取广告容器高度
    });

    window.addEventListener("scroll", this.handleScroll); // 监听滚动事件
  },
  unmounted() {
    window.removeEventListener("scroll", this.handleScroll); // 移除滚动事件监听
  },
  methods: {
    handleScroll() {
      const scrollTop = window.scrollY || window.pageYOffset; // 获取当前滚动位置
      this.lastScrollTop = scrollTop; // 更新上一次滚动位置

      // 只更新 ADV3 的样式
      this.updateAdStyles();
    },
    getAdTopOffsets() {
      // 获取每个广告容器顶部偏移量
      if (this.$refs.advertisements) {
        return this.$refs.advertisements.map(ref => ref.offsetTop);
      }
      return [];
    },
    shouldFixAd(index) {
      // 确保 $refs.advertisements 存在且有足够的长度
      if (this.$refs.advertisements && this.$refs.advertisements.length > 1) {
        // 只有 ADV3 在其顶部与页面顶部对齐时才固定
        const adv2Bottom = this.$refs.advertisements[1].getBoundingClientRect().bottom;
        if (index === 2 && adv2Bottom <= 0) {
          return true;
        }
      }
      return false;
    },
    updateAdStyles() {
      // 根据当前滚动位置更新 ADV3 的样式
      if (this.$refs.advertisements && this.$refs.advertisements.length > 1) {
        this.adStyles = this.adTopOffsets.map((offset, index) => ({
          position: index === 2 && this.$refs.advertisements[1].getBoundingClientRect().bottom <= 0 ? 'fixed' : 'static',
          top: index === 2 && this.$refs.advertisements[1].getBoundingClientRect().bottom <= 0 ? '0' : 'auto',
          width: '100%',
          zIndex: index === 2 && this.$refs.advertisements[1].getBoundingClientRect().bottom <= 0 ? 1000 : 'auto'
        }));
      }
    }
  }
};
</script>

<style scoped>
.advertisement-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start; /* 左对齐 */
  position: relative; /* 相对定位 */
}

.advertisement {
  margin-bottom: 20px; /* 广告之间的间距 */
}

.fixed-advertisement {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000;
  transition: top 0.3s ease; /* 过渡效果 */
}

.advertisement img {
  max-width: 100%; /* 图片宽度自适应 */
}
</style>
